<template>
  <div class="rank">
    <div class="empty"></div>
    <div class="top">
      <van-image
        width="100%"
        fit="contain"
        src="https://s3.music.126.net/mobile-new/img/hot_music_bg_2x.jpg?f01a252389c26bcf016816242eaa6aee="
      />
      <div class="cloud"></div>
      <div class="title"></div>
    </div>
    <ul>
      <HotRank
        v-for="(song, index) in songs"
        :key="song.id"
        :song="song"
        :index="index"
        @rank-update-playlist="updatePlaylist"
      ></HotRank>
    </ul>
  </div>
</template>

<script>
import HotRank from "@/components/HotRank.vue";

export default {
  name: "Rank",
  props: ["paused", "currentmusic"],
  components: {
    HotRank,
  },
  data() {
    return {
      ids: [],
      songs: [],
    };
  },
  created() {
    console.log("rank----created");

    this.$toast.loading({
      message: "加载中...",
      forbidClick: true,
      duration: 0,
    });

    this.axios.get("/top/list", { params: { idx: 1 } }).then((response) => {
      this.ids = response.data.playlist.trackIds.slice(0, 30);
      this.ids = this.ids.map(function (item) {
        return item.id;
      });
      this.ids = this.ids.join(",");

      this.axios.get(`/song/detail?ids=${this.ids}`).then((response) => {
        this.songs = response.data.songs;
        this.$toast.clear();
      });
    });
  },
  methods: {
    updatePlaylist() {
      this.songs = this.songs.map((element) => {
        if (!element.song) {
          element.song = {};
          element.song.artists = element.ar;
        }
        return element;
      });
      this.$store.commit("updatePlaylist", this.songs);
    },
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      // console.log('beforeRouteEnter');
      vm.$store.commit("updateActiveid", 2);
    });
  },
};
</script>

<style lang="less" scoped>
.rank {
  background-color: #fff;
  .empty {
    width: 100%;
    height: 86px;
  }
  .top {
    position: relative;
    .cloud {
      background: url("../assets/images/hotmusic.png") no-repeat -57px -60px;
      width: 87px;
      height: 28px;
      position: absolute;
      top: 15px;
      left: 20px;
      transform: scale(0.5);
    }
    .title {
      position: absolute;
      background: url("../assets/images/hotmusic.png") no-repeat -48px -98px;
      width: 283px;
      height: 95px;
      transform: scale(0.5);
      top: 20px;
      left: -40px;
    }
  }
  ul{
    margin-bottom: 50px;
  }
}
</style>